<template>
  <iframe
    ref="iframe"
    src="https://nobodydd.github.io/JHL_share/#/"
    frameborder="0"
    width="100%"
    height="100%"
  ></iframe>
</template>

<script>
export default {
  mounted() {
    window.addEventListener(
      "message",
      function(params) {
        console.log("外部收到message")
        console.log(params);
      },
      false
    );
    setInterval(() => {
        this.$refs.iframe.contentWindow.postMessage("iframe 收到message","*")
        console.log("外部发送postmessage")
    }, 5000);
  },
  methods: {}
};
</script>

